.wrapper {
  background-color: #F8F3FF;
  width: 1440px;
  font-family: Montserrat;
  margin: auto;
  font-size: 16px;
}

.page-header {
  height: 96px;
  display: flex;
  padding: 26px 173px 26px 64px;
  box-sizing: border-box;
}

.logo {
  height: 44px;
}

.nav {
  font-size: 16px;
  line-height: 20px;
  margin-left: auto;
}
.nav__item {
  text-decoration: none;
  color: #8E43ED;
  padding-right: 32px;
}
.nav__item:last-child {
  padding-right: 0;
}

@media only screen and (max-width: 375px) {
  .wrapper {
    width: 375px;
    font-size: 14px;
  }

  .page-header {
    height: 64px;
    padding: 0;
  }

  .logo {
    height: 30px;
    margin: auto;
  }

  .nav {
    display: none;
  }
}
h1 {
  font-weight: 800;
  font-size: 56px;
  line-height: 72px;
}

.hero {
  background-color: #fff;
  height: 800px;
  display: grid;
  padding: 100px 173px 100px 64px;
  box-sizing: border-box;
  column-gap: 32px;
  border-radius: 25% 0;
  position: relative;
}
.hero::after {
  content: url(../pic/Rectangles3.png);
  position: absolute;
  bottom: 45px;
  left: 0;
}
.hero__userPhoto {
  border-radius: 32px;
  width: 528px;
  height: 600px;
  grid-column: 1/2;
}
.hero__info {
  grid-column: 2/3;
}
.hero__info h1 {
  margin-bottom: 24px;
  margin-top: 44px;
}
.hero__info__brif {
  display: flex;
  padding-right: 129px;
}
.hero__info__brif .brif_item:not(:last-child) {
  margin-right: 32px;
}
.hero__info__aboutMe {
  font-size: 18px;
  line-height: 32px;
  font-weight: 400;
}
.hero__info__aboutMe h3 {
  margin-top: 64px;
  margin-bottom: 24px;
  font-weight: 800;
  font-size: 27px;
  line-height: 36px;
}

@media only screen and (max-width: 375px) {
  .hero {
    width: 375px;
    border-radius: 0;
    padding: 24px 0 58px;
    display: flex;
    flex-direction: column;
    height: auto;
  }
  .hero::after {
    content: url(../pic/Rectangles4.png);
    position: absolute;
    top: 305px;
    left: 285px;
  }
  .hero__userPhoto {
    width: 348px;
    height: 396px;
    align-self: center;
  }
  .hero__info {
    width: 348px;
    align-self: center;
  }
  .hero__info__aboutMe {
    font-size: 15px;
    line-height: 24px;
    font-weight: 400;
  }
  .hero__info__aboutMe h3 {
    margin-top: 32px;
    margin-bottom: 16px;
    font-weight: 800;
    font-size: 20px;
    line-height: 24px;
  }
  .hero h1 {
    font-weight: 800;
    font-size: 40px;
    line-height: 48px;
  }
  .hero__brif {
    padding-right: 0;
    display: grid;
    grid-template-areas: "birthday    gender      country" "birthValue genderValue countryValue";
  }
  .hero .brif_item:not(:last-child) {
    margin-right: 39px;
  }
  .hero .brif_item:not(:last-child) .key {
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
  }
  .hero .brif_item:first-child {
    min-width: 110px;
  }
