.key {
  margin-right: 8px;
  font-weight: bold;
  font-size: 15px;
  color: #AA7ECD;
}

.value {
  font-weight: 500;
  font-style: normal;
  font-size: 15px;
  color: #303030;
}

.formContainer {
  padding-left: 64px;
  padding-top: 56px;
  position: relative;
  height: 750px;
  box-sizing: border-box;
}
.formContainer::before {
  content: url(../pic/Rectangles2.png);
  position: absolute;
  bottom: -70px;
  left: 0;
}
.formContainer::after {
  content: url(../pic/Rectangles1.png);
  position: absolute;
  bottom: -98px;
  right: 0;
}
.formContainer h2 {
  font-weight: 800;
  font-size: 40px;
  line-height: 72px;
  color: #303030;
  margin-bottom: 40px;
  position: relative;
  z-index: 3;
}

.form {
  position: relative;
  display: grid;
  grid-template-columns: 304px 304px 304px;
  grid-template-areas: "userName     userGender   ." "userCountry  userCity     userBirthday" "userDocument userDocument userDocument" "getDocument  .            ." "submit       checkbox     .";
  gap: 40px 32px;
}
.form .disabled {
  display: none;
}
.form__field {
  width: 304px;
  height: 56px;
  border: 1px solid #D9BBFF;
  box-sizing: border-box;
  border-radius: 12px;
  padding: 16px;
  padding-right: 26px;
  margin-top: 8px;
