    height: 50px;
    background: #000;
    border-left: 2px solid #8E43ED;;
    border-bottom: 2px solid #8E43ED;
    transform: rotate(-45deg);
  } */
}
.form__field_big {
  width: 976px;
  height: 104px;
  opacity: 0;
  visibility: hidden;
  position: absolute;
}
.form__field_small {
  width: 212px;
