}
.form .getDocument img {
  position: absolute;
  width: 40px;
  height: 40px;
}
.form .getDocument .fileName {
  position: absolute;
  top: 8px;
  left: 60px;
  font-size: 14px;
  color: #303030;
}
.form .getDocument .fileSize {
  position: absolute;
  bottom: 8px;
  left: 60px;
  font-size: 12px;
  color: #AFAFAF;
}
.form .getDocument i {
  position: absolute;
