  bottom: 22px;
  right: 22px;
  color: #8E43ED;
}
.form .btn_submit {
  height: 56px;
  margin-top: auto;
  background: #DED9E4;
  border-radius: 12px;
  color: #fff;
}
.form .btn_active {
  background-color: #8E43ED;
}
.form__checkboxContainer {
  padding-top: 17px;
  margin-left: -100px;
}
.form .checkbox:checked,
.form .checkbox:not(:checked) {
  position: absolute;
  left: -9999px;
}
.form .checkbox:checked + label,
.form .checkbox:not(:checked) + label {
  display: inline-block;
  position: relative;
  padding-left: 28px;
  line-height: 20px;
  cursor: pointer;
}
.form .checkbox:checked + label::before,
.form .checkbox:not(:checked) + label::before {
  content: "";
  position: absolute;
  left: 0px;
  top: 0px;
  width: 18px;
  height: 18px;
  border: none;
  background-color: #E1FEE8;
  border-radius: 2px;
}
.form .checkbox:checked + label:after,
.form .checkbox:not(:checked) + label::after {
  content: "";
  position: absolute;
  transition: all 0.2s ease;
  left: 3px;
  top: 4px;
  width: 10px;
  height: 5px;
  border-radius: 1px;
  border-left: 4px solid #3DD73A;
  border-bottom: 4px solid #3DD73A;
  transform: rotate(-45deg);
}
.form .checkbox:checked + label::after {
  opacity: 1;
}
.form .checkbox:not(:checked) + label::after {
  opacity: 0;
}

