.mobileSlider {
  display: none;
}

.slider {
  background-color: #fff;
  padding: 56px 64px;
}
.slider__pics {
  width: 1312px;
  padding-top: 535px;
  position: relative;
  margin: 20px auto;
  overflow: hidden;
}
.slider .arrow {
  width: 18px;
  height: 18px;
  border-top: 4px solid #fff;
  border-left: 4px solid #fff;
  position: absolute;
  top: 50%;
  z-index: 2;
  cursor: pointer;
}
.slider .left {
  left: 32px;
  transform: rotate(-45deg);
  transform-origin: 0 100%;
  margin-left: 32px;
}
.slider .right {
  right: 32px;
  transform: rotate(135deg);
  transform-origin: 100% 50%;
  margin-right: 32px;
}
.slider__pics > img {
  width: 1312px;
  height: 535px;
  position: absolute;
  border-radius: 32px;
  top: 0px;
  left: 0px;
  transform: translateX(-1312px);
  transition: all 0.5s linear;
  opacity: 0;
}
.slider__pics .sliderRadio:checked ~ img {
  transform: translateX(1312px);
}
.slider__pics .sliderRadio:checked + img {
  transform: translateX(0px);
  opacity: 1;
  z-index: 1;
}
.slider__pics .sliderRadio:nth-of-type(1) {
  position: absolute;
  bottom: 32px;
  left: 626px;
  z-index: 10;
}
.slider__pics .sliderRadio:nth-of-type(2) {
  position: absolute;
  bottom: 32px;
  right: 650px;
  z-index: 10;
}
.slider__pics .sliderRadio:nth-of-type(3) {
  position: absolute;
  bottom: 32px;
  right: 626px;
  z-index: 10;
}
.slider__pics .sliderRadio {
  border-color: #fff;
}
.slider .slider__header {
  position: absolute;
  top: 196px;
  left: calc(50% - 235px);
  font-weight: 800;
  font-size: 56px;
  line-height: 72px;
  color: #fff;
  z-index: 2;
}
.slider .slider__text {
  font-weight: 500;
  font-size: 18px;
  line-height: 32px;
  text-align: center;
  width: 580px;
  position: absolute;
  top: 304px;
  left: calc(50% - 290px);
  color: #fff;
  z-index: 2;
}

